<template>
  <div v-if="showVideo" class="video-slide">
    <iframe
      id="youtube-video"
      width="0"
      height="0"
      src="https://www.youtube.com/embed/8Xfraz9Udec?enablejsapi=1&version=3&playerapiid=ytplayer"
      modestbranding="0"
      controls="0"
      frameborder="0"
      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    ></iframe>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showVideo: true,
    };
  },
  methods: {
    getTime() {
      let options = {
          timeZone: "Europe/Rome",
          year: "numeric",
          month: "numeric",
          day: "numeric",
        },
        formatter = new Intl.DateTimeFormat([], options);
      const currentDate = formatter.format(new Date());
      const startDate = formatter.format(new Date("2022-09-17"));
      const endDate = formatter.format(new Date("2022-09-18"));

      let hours = new Date().getUTCHours() + 2;

      if (currentDate >= startDate && currentDate <= endDate) {
        if (currentDate === startDate) {
          if (hours >= 14 && hours <= 23) {
            this.showVideo = true;
          } else {
            this.showVideo = false;
          }
        } else if (currentDate === endDate) {
          if (hours >= 14 && hours <= 17) {
            this.showVideo = true;
          } else {
            this.showVideo = false;
          }
        }
      } else {
        this.showVideo = false;
      }
    },
  },

  created() {
    this.getTime();
  },
};
</script>
<style scoped lang="scss">
.video-slide {
  width: 100%;
  background: #ddcfb2;
}

iframe {
  width: 100vw;
  height: calc(100vw / 1.77);
}
</style>