          <span v-if="during">{{ $t("marquee.during.title") }}</span>
          <span v-if="after">{{ $t("marquee.after.title") }}</span>
        </div>
        <div class="mobile-notice-left me-3 d-block d-md-none">WATCH</div>
        <div class="notice-right flex-shrink-1">
          <div class="ticker-wrap">
            <div class="ticker">
              <span class="item-collection-1">
                <span
                  v-if="before"
                  v-html="$t('marquee.before.banner')"
